import React, { Fragment, useEffect } from 'react';

import { useImmer } from 'use-immer';
import ServerTime from '../ServerTime/ServerTime';
import axios from 'axios';

import { withRouter, Redirect, Link } from 'react-router-dom';

import { Icon, Menu, Segment, Sidebar, Header } from 'semantic-ui-react';
import { connect } from 'react-redux';

const SideNav = ({ user, children, history }) => {
  useEffect(() => {
    const getMessagesNotRead = async () => {
      try {
        const msgs = await axios.get('/api/users/geturmessagescount');
        setMessageCount((draft) => msgs.data);
        const news = await axios.get('/api/users/geturnewscount');
        setNewsCount((draft) => news.data);
      } catch (err) {
        console.error(err.response);
      }
    };

    getMessagesNotRead();
  });
  const [messageCount, setMessageCount] = useImmer(Number);
  const [newsCount, setNewsCount] = useImmer(Number);
  return (
    <Fragment>
      <div
        style={{
          position: 'fixed',
          zIndex: 1,
          display: 'flex',
          flexDirection: 'column',
          top: 0,
          bottom: 0,
          left: 0,
          width: '250px',
          background: '#1B1C1D',
          overflowX: 'hidden',
          flex: 1,
        }}
      >
        <div
          style={{
            flex: 1,
            overflowY: 'auto',
          }}
        >
          <Menu vertical inverted size="massive">
            <Menu.Item>
              <ServerTime />
            </Menu.Item>
            <Menu.Item>
              <Menu.Menu>
                <Menu.Item
                  name="Home"
                  onClick={() => {
                    history.push('/homepage');
                  }}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="My Profile"
                  onClick={() => {
                    history.push(`/profile/${user.userid}`);
                  }}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Explore Land"
                  onClick={() => {
                    history.push('/explore', user);
                  }}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name={'Messages' + (messageCount !== 0 ? messageCount : '')}
                  onClick={() => history.push('/messages')}
                  style={{
                    fontSize: 'x-large',
                    color: messageCount !== 0 && 'yellow',
                  }}
                />
                <Menu.Item
                  name={'My News' + (newsCount !== 0 ? newsCount : '')}
                  onClick={() => history.push('/news')}
                  style={{
                    fontSize: 'x-large',
                    color: newsCount !== 0 && 'yellow',
                  }}
                />

                <Menu.Item
                  name="Buildings"
                  onClick={() => history.push('/buildings')}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Military"
                  onClick={() => history.push('/military')}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Attack"
                  onClick={() => history.push('/attack')}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Intelligence"
                  onClick={() => history.push('/intelligence')}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Scoreboard"
                  onClick={() => history.push('/scoreboard')}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Ask Questions"
                  onClick={() => history.push('/askquestions')}
                  style={{ fontSize: 'x-large' }}
                />
                <Menu.Item
                  name="Job Listings"
                  onClick={() => history.push('/joblistings')}
                  style={{ fontSize: 'x-large' }}
                />
              </Menu.Menu>
            </Menu.Item>
          </Menu>
        </div>
      </div>
      {children}
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  user: state.user,
});

export default connect(mapStateToProps)(withRouter(SideNav));
