import React, { useEffect } from 'react';
import {
  Grid,
  Header,
  Table,
  Input,
  Button,
  Modal,
  Icon,
} from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import axios from 'axios';

const Buildings = () => {
  useEffect(() => {
    const getBuildings = async () => {
      try {
        const result = await axios.get('/api/users/getmyself');
        setBuildingQty((draft) => {
          draft.intel = result.data.buildings.intelligenceCamp.quantity;
          draft.armyCamp = result.data.buildings.infantryCamp.quantity;
          draft.airField = result.data.buildings.airField.quantity;
          draft.navalBase = result.data.buildings.navalBase.quantity;
        });
      } catch (err) {
        console.error(err.response.data);
      }
    };
    getBuildings();
  }, []);

  const [open, setOpen] = React.useState(false);
  const [buildingQty, setBuildingQty] = useImmer({
    intel: Number,
    armyCamp: Number,
    airField: Number,
    navalBase: Number,
  });
  const [intelDiv, setIntelDiv] = useImmer(0);
  const [armyCamp, setArmyCamp] = useImmer(0);
  const [airField, setAirField] = useImmer(0);
  const [navalBase, setNavalBase] = useImmer(0);
  //const [foodBank, setFoodBank] = useImmer(0);
  const [buildingTasks, setBuildingTasks] = useImmer([]);

  const getBuildingTasks = async () => {
    try {
      const result = await axios.get('/api/actions/getbuildingtasks');
      console.log(result.data);
      setBuildingTasks((draft) => result.data);
    } catch (err) {
      console.error(err.response);
    }
  };

  const onChange = (e) => {
    if (e.target.name === 'armyCamp') {
      setArmyCamp((draft) => e.target.value);
    }
    if (e.target.name === 'airField') {
      setAirField((draft) => e.target.value);
    }
    if (e.target.name === 'navalBase') {
      setNavalBase((draft) => e.target.value);
    }
    if (e.target.name === 'intelDiv') {
      setIntelDiv((draft) => e.target.value);
    }
  };

  const onSubmit = async (e) => {
    try {
      e.preventDefault();
      const structure = {
        intelDiv,
        armyCamp,
        airField,
        navalBase,
      };
      const res = await axios.post('/api/actions/buildbuildings', structure);
      console.log(res.data);
      setIntelDiv((draft) => 0);
      setArmyCamp((draft) => 0);
      setAirField((draft) => 0);
      setNavalBase((draft) => 0);
      alert('ok');
    } catch (err) {
      console.error(err.response);
    }
  };

  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: 550 }}>
        <Header as="h3">Buildings</Header>
        <Table celled padded>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Building</Table.HeaderCell>
              <Table.HeaderCell>Cost</Table.HeaderCell>
              <Table.HeaderCell>Extra Space per unit</Table.HeaderCell>
              <Table.HeaderCell>Land Req per unit</Table.HeaderCell>
              <Table.HeaderCell>You have</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            <Table.Row>
              <Table.Cell>
                <Header>Intelligence Dept</Header>
              </Table.Cell>
              <Table.Cell singleLine>$500</Table.Cell>
              <Table.Cell singleLine>5</Table.Cell>
              <Table.Cell singleLine>10</Table.Cell>
              <Table.Cell>{buildingQty.intel}</Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  name="intelDiv"
                  value={intelDiv}
                  onChange={onChange}
                  type="number"
                />
              </Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>
                <Header>Army Camp</Header>
              </Table.Cell>
              <Table.Cell singleLine>$500</Table.Cell>
              <Table.Cell singleLine>5</Table.Cell>
              <Table.Cell singleLine>10</Table.Cell>
              <Table.Cell>{buildingQty.armyCamp}</Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  name="armyCamp"
                  value={armyCamp}
                  onChange={onChange}
                  type="number"
                />
              </Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>
                <Header>Air Field</Header>
              </Table.Cell>
              <Table.Cell singleLine>$500</Table.Cell>
              <Table.Cell singleLine>5</Table.Cell>
              <Table.Cell singleLine>10</Table.Cell>
              <Table.Cell>{buildingQty.airField}</Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  name="airField"
                  value={airField}
                  onChange={onChange}
                  type="number"
                />
              </Table.Cell>
            </Table.Row>
            <Table.Row>
              <Table.Cell>
                <Header>Naval Base</Header>
              </Table.Cell>
              <Table.Cell singleLine>$500</Table.Cell>
              <Table.Cell singleLine>5</Table.Cell>
              <Table.Cell singleLine>10</Table.Cell>
              <Table.Cell>{buildingQty.navalBase}</Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  name="navalBase"
                  value={navalBase}
                  onChange={onChange}
                  type="number"
                />
              </Table.Cell>
            </Table.Row>
            {/* <Table.Row>
              <Table.Cell>
                <Header>Food Bank</Header>
              </Table.Cell>
              <Table.Cell singleLine>10</Table.Cell>
              <Table.Cell>10</Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  name="foodBank"
                  value={foodBank}
                  onChange={onChange}
                  type="number"
                />
              </Table.Cell>
           </Table.Row>*/}
          </Table.Body>
        </Table>

        <Button color="black" fluid onClick={onSubmit}>
          Build
        </Button>
        <Modal
          open={open}
          onClose={() => setOpen(false)}
          onOpen={() => {
            setOpen(true);
            getBuildingTasks();
          }}
          trigger={
            <Button color="red" fluid>
              View On-going tasks
            </Button>
          }
        >
          <Modal.Header>Recent Battles</Modal.Header>
          <Modal.Content scrolling>
            <Modal.Description>
              <Table celled padded>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Building</Table.HeaderCell>
                    <Table.HeaderCell>Amount</Table.HeaderCell>
                    <Table.HeaderCell>Returning In</Table.HeaderCell>
                    <Table.HeaderCell>Date</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {buildingTasks.map((task, index) => (
                    <Table.Row key={index}>
                      <Table.Cell>
                        <Header>{task.creation.name}</Header>
                      </Table.Cell>
                      <Table.Cell singleLine>
                        {task.creation.amount}{' '}
                      </Table.Cell>
                      <Table.Cell singleLine>
                        {task.doneInWhatTick} Tick
                      </Table.Cell>
                      <Table.Cell>{task.date}</Table.Cell>
                    </Table.Row>
                  ))}
                </Table.Body>
              </Table>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={() => setOpen(false)} color="black">
              Close <Icon name="close" />
            </Button>
          </Modal.Actions>
        </Modal>
      </Grid.Column>
    </Grid>
  );
};

export default Buildings;
